import React, { Component } from 'react'

export class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
      }
      toggleCategories() {

        if(this.state.activeTab === 0){
          return(
            <div>
                <h1>All</h1>
            </div>
          )
        } else if(this.state.activeTab === 1) {
          return (
            <div><h1>iOS</h1></div>
          )
        } else if(this.state.activeTab === 2) {
          return (
            <div><h1>React</h1></div>
          )
        } else if(this.state.activeTab === 3) {
          return (
            <div><h1>Python</h1></div>
          )
        }
      }
    render() {
        return (
            <div className="container">
                <a href="/" class="btn btn-rabbit back"> <i class="fa fa-angle-left" aria-hidden="true"></i> Back to Home </a>
                <div>
                   <center> <h1>My Projects</h1></center>
                </div>
                <br></br>
                <br></br>
                <div className="container">
                <ul class="nav nav-tabs " id="myTab" role="tablist">
  <li class="nav-item col-sm-3 col-md-3">
    <a class="nav-link active" onClick={()=>{
        this.setState ( {
            activeTab : 0
        })
    }} id="home-tab" data-toggle="tab" role="tab" aria-controls="home" aria-selected="true">All</a>
  </li>
  <li class="nav-item col-sm-3 col-md-3">
    <a class="nav-link" 
     onClick={()=>{
        this.setState({
            activeTab : 1
        })
    }}
    role="tab" aria-controls="profile" aria-selected="false">iOS</a>
  </li>
  <li class="nav-item col-md-3 col-sm-3">
    <a class="nav-link" id="contact-tab" 
          id="profile-tab" data-toggle="tab" onClick={()=>{
            this.setState({
                activeTab : 2
            })
        }}data-toggle="tab" role="tab" aria-controls="contact" aria-selected="false">React</a>
  </li>
  <li class="nav-item col-md-3 col-sm-3">
    <a class="nav-link" id="contact-tab" 
          id="profile-tab" data-toggle="tab" onClick={()=>{
            this.setState({
                activeTab : 3
            })
        }}data-toggle="tab" role="tab" aria-controls="contact" aria-selected="false">Python</a>
  </li>
</ul>
<br></br>
<br></br>
<div class="tab-content" id="myTabContent">
  {this.toggleCategories()}
</div>
            </div>
            </div>
        )
    }
}

export default Projects
